<template>
  <div class="p-ranking">
    <div class="p-ranking__content">
      <!--  -->
      <div class="p-ranking__content-sort">
        <ul>
          <li>
            <button
              v-on:click="sortBy('hour')"
              v-bind:class="[ activetab === 1 ? 'active': '']"
            ></button>
          </li>
          <li>
            <button
              v-on:click="sortBy('day')"
              v-bind:class="[ activetab === 2 ? 'active' : '']"
            ></button>
          </li>
          <li>
            <button
              v-on:click="sortBy('week')"
              v-bind:class="[ activetab === 3 ? 'active' : '']"
            ></button>
          </li>
        </ul>
      </div>

      <!-- -->
      <div class="p-ranking__content-filter">
        <select v-model="preview" multiple class="c-filter">
          <option class="c-filter__option" disabled>----</option>
          <option
            class="c-filter__option"
            v-bind:value="check"
            v-for="(check, index) in check_lists"
            :key="index"
          >{{ check }}</option>
        </select>
      </div>

      <!--  -->
      <div class="p-ranking__content-information">
        <p>
          <span>{{ time }}</span> 
        </p>
      </div>

      <!--  -->
      <div class="p-ranking__content-table">
        <table class="c-table">
          <thead class="c-table__thead">
            <tr>
              <th width="10"></th>
              <th width="40"></th>
              <th width="25">24</th>
              <th width="25">24</th>
            </tr>
          </thead>
          <tbody class="c-table__tbody">
            <tr v-for="(bland, index) in selectedBlands" :key="bland.id">
              <td width="10">
                <a v-bind:href="bland.url" target="_blank">{{ index + 1 }}</a>
              </td>
              <td width="40">
                <a v-bind:href="bland.url" target="_blank">{{ bland.name }}</a>
              </td>
              <td width="25">
                <a v-bind:href="bland.url" target="_blank">{{ bland.high }}</a>
              </td>
              <td width="25">
                <a v-bind:href="bland.url" target="_blank">{{ bland.low }}</a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BlandPanel",
  data() {
    return {
      check_lists: [
        //filter
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      preview: [], //
      blands: [], //
      time: [], //DB
      sortKey: "", //
      activetab: 1 //
    };
  },

  mounted: function() {
    console.log("mounted");
    axios
      .get("/api/ranking")
      .then(response => {
        // 
        this.time = response.data[0];
        //  
        this.blands = response.data[1];
      })
      .catch(response => console.log(response));
    // 1
    this.sortBy("hour");
  },
  computed: {
    /**
     * sort&fileter
     */
    selectedBlands: function() {
      // sortKeyswitch
      switch (this.sortKey) {
        case "hour":
          console.log("");
          this.activetab = 1;
          this.blands.sort(function(a, b) {
            if (a.hour_tweets_count > b.hour_tweets_count) return -1;
            if (a.hour_tweets_count < b.hour_tweets_count) return 1;
            return 0;
          });
          break;

        case "day":
          console.log("");
          this.activetab = 2;
          this.blands.sort(function(a, b) {
            if (a.day_tweets_count > b.day_tweets_count) return -1;
            if (a.day_tweets_count < b.day_tweets_count) return 1;
            return 0;
          });
          break;

        case "week":
          console.log("");
          this.activetab = 3;
          this.blands.sort(function(a, b) {
            if (a.week_tweets_count > b.week_tweets_count) return -1;
            if (a.week_tweets_count < b.week_tweets_count) return 1;
            return 0;
          });
          break;
        default:
      }
      // ,preview
      if (this.preview.length === 0) {
        return this.blands;
      }
      // preview
      return this.blands.filter(function(bland) {
        return this.preview.includes(bland.name);
      }, this);
    }
  },
  /**
   * 
   */
  methods: {
    sortBy: function(key) {
      console.log("methods");
      this.sortKey = key;
    }
  }
};
</script>
